/* eslint-disable react/prop-types */
import React, { useState, useEffect } from 'react'
import {
  Card,
  CardHeader,
  Grid,
  Container,
  Box,
  Typography,
} from '@material-ui/core'
import Expenses from './pengakollen/expenses'
import ExpenseAmountMoney from './pengakollen/expenseAmountMoney'
import { firebase } from '../config/fbConfig'
import Banner from './assets/banner'

const Pengakollen = ({ usedUser }) => {
  const hasCompany = 'companies' in usedUser
  const [userInfo, setUserInfo] = useState('')
  const [company, setCompany] = useState({
    facebook: '-',
    instagram: '-',
    website: '-',
  })

  useEffect(async () => {
    setUserInfo(usedUser)
    const userDocRef = firebase
      .firestore()
      .collection('companies')
      .doc(usedUser.companies[0].companyId)
    const dataExists = await userDocRef.get()
    setCompany(dataExists.data())
  }, [])

  if (!hasCompany)
    return (
      <Container>
        <Card
          style={{ background: 'white', paddingTop: 20, paddingBottom: 20 }}
        >
          <Typography align="center" variant="h4">
            Start or join a company first
          </Typography>
        </Card>
      </Container>
    )
  return (
    <Container>
      <Card>
        <Banner text="Pengakollen" />
        <CardHeader
          subheader="Att hlla koll p ekonomin r viktigt. Pengar in (intkter) och pengar ut (utgifter) berttar om ditt fretags resultat - det vill sga hur mycket vinst eller frlust ditt fretag gr.
        ,,
          Glm inte bort att bokfra varje kp eller frsljning. Avvara lite tid nr din arbetsdag r ver s har du stenkoll p din ekonomi under sommaren.
          "
          title="Hll koll p ekonomin!"
        />
      </Card>
      <Grid container spacing={3} style={{ marginTop: 10 }}>
        <Grid item xs={12} md={6}>
          <Expenses usedUser={usedUser} expense />
        </Grid>
        <Grid item xs={12} md={6}>
          <Expenses usedUser={usedUser} />
        </Grid>
        <Grid item xs={12}>
          <ExpenseAmountMoney usedUser={usedUser} />
        </Grid>
      </Grid>
    </Container>
  )
}

export default Pengakollen
